"use strict";

import { tns } from "tiny-slider/src/tiny-slider";
import { createRipple, placeLineY, setCounter } from "../../../js/import/functions";

if (document.querySelector(".rooms")) {
  let rooms_slider = tns({
    container: ".rooms-preview-list__item--active .rooms-preview-slider__item",
    items: 1,
    slideBy: "page",
    autoplay: true,
    touch: true,
    mouseDrag: true,
    prevButton: ".rooms-preview-list__item--active .slider__prev", 
    nextButton: ".rooms-preview-list__item--active .slider__next",
    controlsContainer: ".rooms-preview-list__item--active .rooms-preview-slider-controls",
  });
  setCounter('rooms', rooms_slider, 'rooms-preview-list__item--active');

  let rooms_list_items = document.querySelectorAll(".rooms-list__item");
  let rooms_underline =  document.querySelector('.rooms .underline');
  let rooms_item_active = document.querySelector(".rooms-list__item--active");
  placeLineY(rooms_underline, {'height': rooms_item_active.clientHeight + 'px','top': rooms_item_active.offsetTop + 'px',});

  rooms_list_items.forEach(function (link) {
    link.addEventListener("click", (event) => {
      placeLineY(rooms_underline, {'height': link.clientHeight + 'px', 'top': link.offsetTop + 'px',});
      //event.preventDefault();
      if (document.documentElement.clientWidth > 768) {
        event.preventDefault();
      }
      let prev_room = document.querySelector(".rooms-preview-list__item--active");
      let prev_link = document.querySelector(".rooms-list__item--active");
      let room = document.querySelector(link.getAttribute("href"));
      room.classList.toggle("rooms-preview-list__item--active");
      rooms_slider.destroy();
      prev_room.classList.toggle("rooms-preview-list__item--active");
      rooms_slider = tns({
        container: ".rooms-preview-list__item--active .rooms-preview-slider__item",
        items: 1,
        slideBy: "page",
        autoplay: true,
        touch: true,
        mouseDrag: true,
        prevButton: ".rooms-preview-list__item--active .slider__prev", 
        nextButton: ".rooms-preview-list__item--active .slider__next",
        controlsContainer: ".rooms-preview-list__item--active .rooms-preview-slider-controls",
      });
      setCounter('rooms', rooms_slider, 'rooms-preview-list__item--active');
      prev_link.classList.toggle("rooms-list__item--active");
      link.classList.toggle("rooms-list__item--active");
    });
    link.addEventListener("click", createRipple);
  });

  let rooms_detail = document.querySelectorAll('.rooms-preview-info-detail');
  rooms_detail.forEach(el => el.addEventListener('click', function(event) {
    el.classList.toggle("rooms-preview-info-detail--active"); 
  }))
}