"use strict";

import { tns } from "tiny-slider/src/tiny-slider";
import { createRipple, placeLineX, setCounter} from "../../../js/import/functions";

if (document.querySelector(".other-rooms")) {
  let other_rooms_slider = tns({
    container: ".other-rooms-preview-list__item--active .other-rooms-preview-slider__item",
    items: 1,
    slideBy: "page",
    autoplay: true,
    touch: true,
    mouseDrag: true,
    prevButton: ".other-rooms-preview-list__item--active .slider__prev", 
    nextButton: ".other-rooms-preview-list__item--active .slider__next",
    controlsContainer: ".other-rooms-preview-list__item--active .other-rooms-preview-slider-controls",
  });
  setCounter('other-rooms', other_rooms_slider, 'other-rooms-preview-list__item--active');

  let other_rooms_list = document.querySelectorAll(".other-rooms-list__item");
  let other_rooms_underline =  document.querySelector('.other-rooms .underline');
  let other_rooms_item_active = document.querySelector(".other-rooms-list__item--active");
  placeLineX(other_rooms_underline, {'width': other_rooms_item_active.clientWidth + 'px','left': other_rooms_item_active.offsetLeft + 'px',});

  other_rooms_list.forEach(function (link) {
    link.addEventListener("click", function (event) {
      placeLineX(other_rooms_underline, {'width': link.clientWidth + 'px', 'left': link.offsetLeft + 'px',});
      if (document.documentElement.clientWidth > 768) {
        event.preventDefault();
      }
      let prev_room = document.querySelector(".other-rooms-preview-list__item--active");
      let prev_link = document.querySelector(".other-rooms-list__item--active");
      let room = document.querySelector(link.getAttribute("href"));
      room.classList.toggle("other-rooms-preview-list__item--active");
      other_rooms_slider.destroy();
      prev_room.classList.toggle("other-rooms-preview-list__item--active");
      other_rooms_slider = tns({
        container: ".other-rooms-preview-list__item--active .other-rooms-preview-slider__item",
        items: 1,
        slideBy: "page",
        autoplay: true,
        touch: true,
        mouseDrag: true,
        prevButton: ".other-rooms-preview-list__item--active .slider__prev", 
        nextButton: ".other-rooms-preview-list__item--active .slider__next",
        controlsContainer: ".other-rooms-preview-list__item--active .other-rooms-preview-slider-controls",
      });
      setCounter('other-rooms', other_rooms_slider, 'other-rooms-preview-list__item--active');
      prev_link.classList.toggle("other-rooms-list__item--active");
      link.classList.toggle("other-rooms-list__item--active");
    });
    link.addEventListener("click", createRipple);
  });

  let other_rooms_detail = document.querySelectorAll('.other-rooms-preview-info-detail');
  other_rooms_detail.forEach(el => el.addEventListener('click', function(event) {
    el.classList.toggle("other-rooms-preview-info-detail--active"); 
  }))
}